<template>
  <section id="site" class="site">
    <!-- <help v-if="needHelp"></help> -->
    <div class="container">
      <div class="site__wrapper">
        <h1 class="site__title visually-hidden">{{site.name}}</h1>
      </div>
    </div>

    <picture class="site__picture">
      <source :srcset="'/images/projects/mobile/' + site.image + ' 1x'" media="(max-width: 480px)">
      <source :srcset="'/images/projects/tablet/' + site.image + ' 1x'" media="(max-width: 1024px)">
      <img class="site__picture--image" 
          :src="'/images/projects/desktop/' + site.image" 
          :alt="site.name"
          width="1903">
    </picture>
    <button type="button" @click="routeToHome" :class="{ site__back: true, site__back_loaded: loaded }">
      <svg class="site__back--icon" enable-background="new 0 0 386.667 386.667" height="512" viewBox="0 0 386.667 386.667" width="512" xmlns="http://www.w3.org/2000/svg"><path fill="#102532" d="m386.667 45.564-45.564-45.564-147.77 147.769-147.769-147.769-45.564 45.564 147.769 147.769-147.769 147.77 45.564 45.564 147.769-147.769 147.769 147.769 45.564-45.564-147.768-147.77z"/></svg>
    </button>
    <div :class="{ 
          site__info: true, 
          site__info_loaded: loaded, 
          site__info_open: showInfo 
          }"
        v-on:mouseenter="changeInfoStatus"
        v-on:mouseleave="changeInfoStatus"
        v-on:focusin="changeInfoStatus"
        v-on:focusout="changeInfoStatus"
        tabindex="0">
      <svg class="site__info--icon" width="46" height="46" viewBox="0 0 46 46" fill="none" xmlns="http://www.w3.org/2000/svg">
        <line x1="25" y1="14" x2="21" y2="14" stroke="#FFFFFF" stroke-width="4"/>
        <line x1="23" y1="19" x2="23" y2="34" stroke="#FFFFFF" stroke-width="4"/>
      </svg>
      <ul class="site__info--list">
        <li class="site__info--item">{{site.name}}</li>
        <li class="site__info--item"><span class="site__info--attribute">URL</span> <a :href="site.url" class="site__info--link" target="_blank">{{url}}</a></li>
        <li class="site__info--item"><span class="site__info--attribute">CMS</span> {{site.cms}}</li>
      </ul>
    </div>
  </section>
</template>

<script>
  import { mapGetters } from 'vuex';
  import Help from '@/components/Help.vue';

  export default {
    components: {
      Help
    },
    mounted() {
      setTimeout(() => {
        this.loaded = true;
      }, 1050);
    },
    data: () => ({
      showInfo: false,
      loaded: false
    }),
    computed: {
      ...mapGetters([
        'getSiteById',
        'getHelp'
      ]),
      id() {
        return this.$route.params.id;
      },
      site() {
        return this.getSiteById(this.id);
      },
      url() {
        let slice = this.site.url.split("https://")

        return slice[1];
      }
    },
    methods: {
      changeInfoStatus(event) {
        let type = event.type;

        switch (type) {
          case "mouseenter":
            this.showInfo = true;
            break;
          case "mouseleave":
            this.showInfo = false;
            break;
          case "focusin":
            this.showInfo = true;
            break;
          case "focusout":
            this.showInfo = false;
            break;
          default:
            this.showInfo = !this.showInfo;
            break;
        }
      },
      routeToHome() {
        this.$router.push({ name: 'Home' });
      }
    }
  }
</script>

<style lang="less">
  @-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(255, 154, 153, 1);
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(255, 154, 153, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(255, 154, 153, 0);
    }
  }
  @keyframes pulse {
    0% {
      -moz-box-shadow: 0 0 0 0 rgba(255, 154, 153, 1);
      box-shadow: 0 0 0 0 rgba(255, 154, 153, 1);
    }
    70% {
        -moz-box-shadow: 0 0 0 10px rgba(255, 154, 153, 0);
        box-shadow: 0 0 0 10px rgba(255, 154, 153, 0);
    }
    100% {
        -moz-box-shadow: 0 0 0 0 rgba(255, 154, 153, 0);
        box-shadow: 0 0 0 0 rgba(255, 154, 153, 0);
    }
  }

  .site {
    position: relative;

    &__wrapper {
      position: relative;
      margin: 0 12px;
    }

    &__title {
      margin: 24px 0;
      font-size: 28px;
    }

    &__info {
      min-width: 46px;
      min-height: 46px;
      padding: 12px;
      background-color: #ff736f;
      opacity: 0;
      margin: 0 32px;
      position: fixed;
      right: 0;
      bottom: 36px;
      z-index: 3;
      border-width: 1px;
      border-style: solid;
      border-color: #ff9a99;
      border-radius: 47.5%;
      box-shadow: 0px 6px 14px -6px rgba(24, 39, 75, 0.12), 0px 10px 32px -4px rgba(24, 39, 75, 0.1);
      transition: background-color 150ms ease 200ms,
                  border-color 150ms ease 200ms,
                  border-radius 150ms ease 200ms,
                  opacity 750ms ease-in;
      animation: pulse 2s infinite;

      &_open {
        background-color: #ffffff;
        border-color: rgba(97, 139, 171, 0.16);
        border-radius: 2px;
        transition: border-radius 75ms;
        animation: none;

        .site__info {
          &--icon {
            opacity: 0;
            visibility: hidden;
            transition: all 150ms ease-out;
          }
          &--list {
            font-size: 16px;

            @media screen and (max-width: 768px) {
              font-size: 14px;
            }
          }
          &--item {
            opacity: 1;
            transition: font-size 350ms ease-out, 
                        opacity 200ms ease-out 150ms;
          }
          &--attribute {
            width: 46px;
          }
        }
      }

      &_loaded {
        opacity: 1;
      }

      @media screen and (max-width: 768px) {
        margin-left: 8px;
        margin-right: 8px;
        bottom: 16px;
      }

      &--icon {
        width: 44px;
        height: 44px;
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 1;
        visibility: visible;
        transition: all 50ms linear 300ms;
      }
      &--list {
        font-size: 0;
      }
      &--item {
        display: flex;
        font-size: 1em;
        opacity: 0;
        transition: font-size 350ms ease-out, 
                    opacity 250ms ease-out;

        &:first-child {
          margin-bottom: 0.5em;
        }
      }
      &--attribute {
        display: block;
        width: 0;
        position: relative;
        margin-right: 10px;
        transition: width 350ms ease-out;

        &::after {
          content: ":";
          position: absolute;
          right: 0;
        }
      }
      &--link {
        color: #1a0dab;
      }
    }
    &__picture {
      &--image {
        display: block;
        max-width: 1903px;
        min-height: 101vh;
        width: 100%;
        height: auto;
        margin: auto;
        background-color: hsl(0, 0%, 89%);
      }
    }
    &__back {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 46px;
      height: 46px;
      background-color: white;
      opacity: 0;
      margin: 0 32px;
      position: fixed;
      right: 0;
      top: calc(46px + 32px);
      z-index: 3;
      border-width: 1px;
      border-style: solid;
      border-color: rgba(97, 139, 171, 0.16);
      border-radius: 47.5%;
      box-shadow: 0px 6px 14px -6px rgba(24, 39, 75, 0.12), 0px 10px 32px -4px rgba(24, 39, 75, 0.1);
      cursor: pointer;
      transition: border-color 150ms ease,
                  opacity 750ms ease-in;

      &:hover, &:focus {
        border-color: rgba(97, 139, 171, 0.7);
      }

      &_loaded {
        opacity: 1;
      }

      @media screen and (max-width: 768px) {
        margin-left: 8px;
        margin-right: 8px;
        top: calc(46px + 16px);
      }

      &--icon {
          width: 18px;
          height: 18px;
      }
    }
  }
</style>